.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 0 auto;
  width: 100%;
  padding: 39px 104px 42px 103px;
  background-color: $color-black-russian;
  color: $color-default-white;

  @include vp-1199 {
    padding: 45px 45px 44px 45px;
  }

  @include vp-767 {
    display: block;
    gap: 28px;
    padding: 54px 15px 55px;
    column-count: 2;
  }
}

.footer__logo {
  padding-top: 9px;

  @include vp-1199 {
    padding-top: 0;
  }

  @include vp-767 {
    margin: 4px 0 25px;
  }

  img {
    max-width: 88px;
    background-color: transparent;

    @include vp-1199 {
      margin-top: -6px;
      width: 82px;
    }

    @include vp-767 {
      max-width: 68px;
    }
  }
}

.footer-nav__list {
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  padding: 0 0 0 10px;

  @include vp-1199 {
    padding: 0 0 0 15px;
  }

  @include vp-767 {
    display: block;
    margin-bottom: 50px;
    padding: 0;
  }
}

.footer-nav__item {
  @include vp-767 {
    width: 100%;
    margin-bottom: 20px;
  }
}

.footer-nav__item:not(:last-of-type) {
  margin-right: 40px;

  @include vp-1199 {
    margin-right: 30px;
  }
}

.footer-nav__link {
  display: block;
  font-family: $ff-secondary;
  font-weight: 400;
  font-size: 14px;
  line-height: 16.41px;
  color: $color-default-white;

  &:hover {
    text-decoration: 1px underline;
    text-underline-offset: 5px;
    color: $color-default-white;
  }

  &:focus-visible {
    outline: 1px solid $color-default-white;
    outline-offset: 5px;
  }

  &:active {
    outline: none;
    text-decoration: none;
  }

  @include vp-767 {
    font-family: $ff-secondary;
    font-weight: 400;
    font-size: 16px;
    line-height: 18.75px;
  }
}

.footer-social__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-767 {
    justify-content: start;
    padding: 0;
    column-span: all;
  }
}

.footer-social__item {
  position: relative;
}

.footer-social__item:not(:last-of-type) {
  margin-right: 11px;
}

.footer-social__link {
  display: block;
  width: 25px;
  height: 25px;
  background-color: $color-default-white;
  border-radius: 50%;

  &:hover,
  &:focus-visible {
    background-color: $color-egyptian-blue;
    outline: none;

    svg {
      fill: $color-default-white;
    }
  }

  &:active {
    background-color: $color-default-sapphire;
  }

  &:disabled {
    background-color: $color-bright-grey;
    fill: $color-black-russian;
    pointer-events: none;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    &:hover,
    &:focus-visible {
      fill: $color-default-white;
    }
  }
}
